
// services/api/locateEngItemService.js

export const locateEngItem = async (identifier) => {
    const response = await fetch(
      "https://oi000186152-us1-space.3dexperience.3ds.com/enovia/resources/v1/modeler/dseng/dseng:EngItem/locate",
      {
        method: "POST",
        headers: {
          "Securitycontext": "VPLMProjectAdministrator.BU-0000001.Rosemount Measurement",
          "ENO_CSRF_TOKEN": "QMF8-WHQB-FW5H-K4JL-UCAI-TC8T-1TKW-NH5K",
          "Content-Type": "application/json",
          "Cookie": "JSESSIONID=35214C79E88A70A1094004926453AEB1; SERVERID=MT_Metadata_0_7025",
        },
        body: JSON.stringify({
          referencedObjects: [
            {
              source: "https://oi000186152-us1-space.3dexperience.3ds.com/enovia",
              type: "VPMReference",
              identifier: identifier,
              relativePath: `/resources/v1/modeler/dseng/dseng:EngItem/${identifier}`,
            },
          ],
        }),
      }
    );
  
    const data = await response.json();
    if (!response.ok) throw new Error(data.message || "Failed to fetch locate data");
    return data;
  };
  